import React from "react";
import { mentorData } from "components/WithMentor/data";
import Slider from "react-slick";
import style from "./style.scss";
import HideArrow from "./HideArrow";
import classNames from "classnames";

export default () => {
  const settings = {
    customPaging: function(i) {
      return (
        <img
          className="img-fluid rounded-circle"
          src={mentorData[i].avatar}
          alt="page"
        />
      );
    },
    fade: true,
    dots: true,
    lazyload: true,
    dotsClass: "slick-dots slick-thumb slick-dots-home",
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2000,
    nextArrow: <HideArrow />,
    prevArrow: <HideArrow />
  };
  return (
    <div className="mb-3">
      <Slider {...settings}>
        {mentorData.map((mentor, index) => (
          <div key={index} className="pb-3">
            <div className="contailer">
              <div className="row align-items-center ">
                <div className="col-12 col-md-4 col-xl-3" align="center">
                  <img
                    src={mentor.avatar}
                    alt="avatar"
                    className={classNames(
                      "rounded-circle box-shadow-default",
                      style.img
                    )}
                  />
                  <h3 className="font-weight-bold h3-responsive text-capitalize mt-3 text-primary">
                    {mentor.name}
                  </h3>
                </div>

                <div className="col-12 col-md-8 col-xl-9 text-md-left text-center">
                  <h2 className="h2-responsive font-weight-bold">
                    {mentor.title}
                  </h2>
                  <h4 className="h4-responsive ">{mentor.desc}</h4>
                </div>
              </div>
            </div>
          </div>
        ))}
      </Slider>
    </div>
  );
};
